.Player {
  margin: 30px 0;
  padding: 5px;
  box-shadow: 0px 0px 5px 5px #CCC; }
  .Player > * {
    margin: 5px; }
  .Player .Player-track-title {
    font-weight: bold; }
  .Player .Player-controls {
    display: flex; }
    .Player .Player-controls .btn {
      width: 25px;
      height: 25px;
      padding: 5px; }
      .Player .Player-controls .btn svg {
        vertical-align: super; }
    .Player .Player-controls .sb-soundplayer-timer {
      margin: 0 5px; }
  .Player div.sb-soundplayer-progress-container {
    height: 5px;
    background-color: #00ABD6; }
    .Player div.sb-soundplayer-progress-container div.sb-soundplayer-progress-inner {
      height: 100%;
      background-color: #DFCF20; }
