#NavDrawer-Drawer h3 {
  margin: 0;
  text-align: center; }

div#NavDrawer-bottom {
  background: #DFCF20;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between; }
  div#NavDrawer-bottom * {
    display: inline; }

@media screen and (min-width: 600px) {
  .NavDrawer-drawer-showing {
    display: none; } }

.NavDrawer-drawer-not-showing {
  display: none;
  box-shadow: 0px -10px  30px #0008;
  text-align: center; }
  .NavDrawer-drawer-not-showing li {
    display: inline;
    text-transform: uppercase; }
  @media screen and (min-width: 600px) {
    .NavDrawer-drawer-not-showing {
      display: block; } }
