iframe {
  max-width: 600px;
  display: block;
  margin: 0 auto; }

div#metamorphosis {
  position: relative;
  min-height: 18vw; }
  @media screen and (min-width: 900px) {
    div#metamorphosis {
      min-height: 14vw;
      margin-bottom: 30px; } }
  div#metamorphosis img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }
    div#metamorphosis img#catterpillar {
      z-index: 0;
      width: 30%;
      animation: catterpillar 0.3s linear 5s 1 forwards; }
    div#metamorphosis img#cacoon {
      transform: translate(-64%, -57%);
      z-index: 1;
      width: 0%;
      animation: cacoon 5.5s linear 5s 1 forwards; }
    div#metamorphosis img#butterfly {
      z-index: 2;
      width: 0%;
      animation: butterfly 0.3s linear 10s 1 forwards; }

div.PortfolioWorks div.works-area p {
  margin-top: 10px; }

div.PortfolioWorks div.works-area div.Player {
  margin: 40px 0 0; }

p.sample-description {
  margin: 0 auto 50px;
  max-width: 600px; }

@keyframes catterpillar {
  from {
    width: 30%; }
  to {
    width: 0%; } }

@keyframes cacoon {
  0% {
    width: 0%; }
  5% {
    width: 25%; }
  95% {
    width: 25%; }
  100% {
    width: 0%; } }

@keyframes butterfly {
  from {
    width: 0%; }
  to {
    width: 30%; } }
