div#TinyTape {
  position: relative; }

@keyframes spin {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }

img.TinyTape-wheel-left {
  animation-name: spin;
  animation-duration: 1200ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  position: absolute;
  left: 23%;
  top: 36%;
  width: 11.5%; }

img.TinyTape-wheel-right {
  animation-name: spin;
  animation-duration: 1000ms;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  position: absolute;
  left: 65%;
  top: 36%;
  width: 11.5%; }
