#Feedback img {
  display: none;
  width: 50%; }
  #Feedback img.robot-mobile {
    display: inline; }

#Feedback div.Feedback-robot {
  width: 50%; }

@media screen and (min-width: 600px) {
  #Feedback img {
    display: block; }
    #Feedback img.robot-mobile {
      display: none; }
  #Feedback div.Feedback-comment {
    display: flex;
    justify-content: center;
    align-items: center; }
    #Feedback div.Feedback-comment:nth-child(odd) {
      flex-flow: row-reverse; }
      #Feedback div.Feedback-comment:nth-child(odd) img {
        transform: scaleX(-1); }
    #Feedback div.Feedback-comment div.Feedback-robot {
      width: 20%; }
      #Feedback div.Feedback-comment div.Feedback-robot img {
        width: 100%; }
    #Feedback div.Feedback-comment p {
      width: 80%; } }
