section#Quiz-labels {
  display: flex;
  justify-content: space-evenly; }

img#boat {
  width: 50%;
  animation: boat 0.75s linear infinite alternate; }

@keyframes boat {
  from {
    transform: rotate(3deg); }
  to {
    transform: rotate(-3deg); } }
  @media screen and (min-width: 600px) {
    img#boat {
      width: 25%; } }

img#waves {
  animation: waves 2s linear infinite alternate;
  position: absolute;
  bottom: 30%;
  left: 0%; }

@keyframes waves {
  from {
    transform: translateX(-5%); }
  to {
    transform: translateX(5%); } }
  @media screen and (min-width: 600px) {
    img#waves {
      width: 50%;
      left: 25%; } }

img#vommypin {
  width: 50px;
  margin-top: -25px;
  margin-bottom: -25px; }

button#Quiz-submit:disabled {
  text-transform: uppercase;
  color: red;
  background: gold; }

div.Quiz-question:nth-of-type(1) textarea {
  height: 2em; }

div.Quiz-question textarea {
  height: 5em; }
