div.App {
  text-align: center; }
  div.App div.padding-and-max-width {
    padding: 5px;
    max-width: 900px;
    margin: auto; }
    @media screen and (min-width: 600px) {
      div.App div.padding-and-max-width {
        padding: 15px; } }

div#App-nav-and-title .App-NavDrawer {
  text-align: right; }

img#App-tiny-sharpie {
  animation: sharpie 0.75s ease-in-out infinite alternate; }

@keyframes sharpie {
  from {
    transform: rotate(2deg); }
  to {
    transform: rotate(-2deg); } }
  @media screen and (min-width: 600px) {
    img#App-tiny-sharpie {
      width: 50vw; } }
  @media screen and (min-width: 900px) {
    img#App-tiny-sharpie {
      width: 30vw; } }
