.User img.User-castle {
  width: 40%; }
  @media screen and (min-width: 600px) {
    .User img.User-castle {
      width: 20%; } }

.User div.User-castle-area {
  position: relative;
  margin: auto; }
  @media screen and (min-width: 600px) {
    .User div.User-castle-area {
      width: 50%; } }
  .User div.User-castle-area #User-castle {
    width: 50%; }
  .User div.User-castle-area #User-rider {
    position: absolute;
    width: 0; }

.play-rideremerges {
  animation: rideremerges 3s linear infinite forwards; }

@keyframes rideremerges {
  0% {
    z-index: -1;
    width: 14%;
    bottom: 5%;
    left: 27%;
    transform: rotate(45deg); }
  30% {
    z-index: -1;
    width: 16%;
    bottom: 3%;
    left: 45%;
    transform: rotate(45deg); }
  40% {
    z-index: 1;
    width: 16%;
    bottom: -6%;
    left: 45%;
    transform: rotate(45deg); }
  50% {
    z-index: 1;
    width: 60%;
    bottom: -20%;
    left: 23%;
    transform: rotate(45deg); }
  60% {
    z-index: 1;
    width: 60%;
    bottom: -6%;
    left: 23%;
    transform: rotate(-10deg); }
  70% {
    z-index: 1;
    width: 60%;
    bottom: -6%;
    left: 23%;
    transform: rotate(0deg); }
  80% {
    z-index: 1;
    width: 60%;
    bottom: -20%;
    left: 23%;
    transform: rotate(45deg); }
  100% {
    width: 60%;
    bottom: -20%;
    left: 100%;
    transform: rotate(45deg); } }

div.User-list-area {
  text-align: left;
  padding: 5px;
  border: 1px solid #DDE; }
