@media screen and (min-width: 600px) {
  div.About div.grid {
    display: grid;
    grid-template-columns: 1fr 1fr; } }

div.About img {
  max-width: 450px; }

div.About #About-react {
  animation: react 5s infinite; }

@keyframes react {
  from {
    transform: rotate(0deg); }
  to {
    transform: rotate(360deg); } }
